/* eslint-disable import/no-duplicates */
/* eslint-disable react/sort-comp */
import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { GROUP_FIELDS, API_URLS, MESSAGES, BREADCRUMBS } from '../../constants';
import formHelper from '../../lib/formHelper';
import GroupProfile from '../../components/GroupProfile/GroupProfile';
import history from '../../history';
import PageWrapper from '../../components/PageWrapper';
import { openNotifier } from '../../actions/notifier';
import withNotifier from '../../actions/withNotifier';

class GroupsCreate extends React.Component {

  static propTypes = {
    openNotifier: PropTypes.func,
  };

  static defaultProps = {
    openNotifier: () => {},
  };

  static contextTypes = { fetch: PropTypes.func.isRequired };

  state = {
    formData: {
      groupName: '',
    },
    users: [],
    groups: [],
    selectedGroups: [],
    selectedUsers: [],
    error: {
      groupName: '',
    },
    loading: false,
  };

  componentDidMount() {
    this.getUsers();
    this.getGroups();
  }

  getUsers = async () => {
    const { fetch } = this.context;
    try {
      const response = await fetch(`${API_URLS.users}`);
      const data = await response.json();
      const users = data.resources;
      this.setState({
        users,
      });
    } catch (e) {
      console.error(e);
    }
  };

  getGroups = async () => {
    const { fetch } = this.context;
    try {
      const response = await fetch(`${API_URLS.groups}`);
      const data = await response.json();
      const groups = data.resources;
      this.setState({
        groups,
      });
    } catch (e) {
      console.error(e);
    }
  };

  isValid = () => {
    const { formData, error } = this.state;
    const { groupName } = formData;
    if (_.some([groupName], formHelper.isInputEmpty)) {
      _.each(GROUP_FIELDS, item => {
        error[item] = formHelper.isInputEmpty(formData[item])
          ? MESSAGES.mandatoryFields
          : '';
      });
    }

    this.setState({ error });

    return _.every(error, formHelper.isInputEmpty);
  };

  createRequestBody = () => {
    const {
      selectedUsers,
      formData,
      users,
      groups,
      selectedGroups,
    } = this.state;
    let members = [];
    members = [
      ..._.map(selectedUsers, user => {
        const userItem = _.find(users, { name: user });
        if (userItem) {
          return {
            type: 'UserReference',
            key: userItem.keys.key,
          };
        }
      }),
      ..._.map(selectedGroups, group => {
        const groupItem = _.find(groups, { groupName: group });
        if (groupItem) {
          return {
            type: 'GroupReference',
            key: groupItem.keys.key,
          };
        }
      }),
    ];
    return { ...formData, members };
  };

  handleSubmit = async e => {
    const { fetch } = this.context;
    e.preventDefault();

    const isValid = await this.isValid();

    if (!isValid) {
      await this.props.openNotifier({
        openNotify: true,
        message: MESSAGES.emptyField,
        variant: 'warning',
      });
      return;
    }

    await this.setState({ loading: true });
    try {
      const response = await fetch(`${API_URLS.groups}`, {
        method: 'POST',
        body: JSON.stringify(this.createRequestBody()),
        headers: {
          'Content-Type': 'application/json',
        },
      });
      const data = await response.json();
      if (response.status === 201) {
        await this.props.openNotifier({
          openNotify: true,
          message: MESSAGES.group.create.success,
          variant: 'success',
        });
        history.push('/user-management');
      } else if (response.status === 400) {
        await this.props.openNotifier({
          openNotify: true,
          message: data.resources,
          variant: 'error',
        });
      }
      await this.setState({ loading: false });
    } catch (er) {
      console.error(er);
    }
  };

  handleChangeText = e => {
    const { formData, error } = this.state;
    const { id, value } = e && e.target;
    formData[id] = value;
    error[id] = formHelper.isInputEmpty(formData[id])
      ? MESSAGES.mandatoryFields
      : '';
    this.setState({ formData, error });
  };

  handleChangeSelectUsers = event => {
    this.setState({ selectedUsers: event.target.value });
  };

  handleChangeSelectGroups = event => {
    this.setState({ selectedGroups: event.target.value });
  };

  onCancel = () => {
    history.push('/user-management');
  };

  render() {
    const {
      formData,
      error,
      users,
      selectedUsers,
      groups,
      selectedGroups,
      loading,
    } = this.state;
    return (
      <PageWrapper breadCrumb={BREADCRUMBS.GROUP_CREATE}>
        <GroupProfile
          formData={formData}
          users={users}
          selectedUsers={selectedUsers}
          groups={groups}
          selectedGroups={selectedGroups}
          error={error}
          handleChangeText={this.handleChangeText}
          handleSubmit={this.handleSubmit}
          handleChangeSelectUsers={this.handleChangeSelectUsers}
          handleChangeSelectGroups={this.handleChangeSelectGroups}
          type="Create Group"
          onCancel={this.onCancel}
          createUser
          loading={loading}
        />
      </PageWrapper>
    );
  }
}

export default connect(null, {
  openNotifier,
})(withNotifier(GroupsCreate));
