/* eslint-disable import/no-extraneous-dependencies */
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import ReactDOM from 'react-dom';
import _ from 'lodash';
import {
  Button,
  withStyles,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  ListItemText,
  OutlinedInput,
  Chip,
  CircularProgress,
} from '../../materialLib';
import { COLORS } from '../../constants';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;

const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const styles = theme => ({
  form: {
    width: '100%', // Fix IE 11 issue.
    margin: `${theme.spacing.unit}px auto 0`,
  },
  submit: {
    backgroundColor: COLORS.NAVY_BLUE,
    marginRight: theme.spacing.unit,
    textTransform: 'capitalize',
  },

  formControl: {
    margin: `0 ${theme.spacing.unit * 5}px ${theme.spacing.unit * 3}px 0`,
    maxWidth: 530,
    width: '100%',
  },
  textField: {
    marginBottom: 20,
  },
  chip: {
    margin: theme.spacing.unit / 4,
    backgroundColor: COLORS.NAVY_BLUE,
    color: COLORS.WHITE,
  },
  btnWrapper: {
    margin: `${theme.spacing.unit * 3}px auto`,
    width: '40%',
  },
  progress: {
    margin: 0,
    color: 'white',
  },
});

const GroupProfile = props => {
  let InputLabelRef = null;
  const {
    classes,
    formData,
    error,
    handleChangeText,
    handleSubmit,
    handleChangeSelectUsers,
    handleChangeSelectGroups,
    type,
    users,
    selectedUsers,
    selectedGroups,
    groups,
    onCancel,
    loading,
  } = props;
  const { groupName } = formData;
  const [labelWidth, setLabelWidth] = useState(0);

  useEffect(
    () =>
      setLabelWidth(
        labelWidth => ReactDOM.findDOMNode(InputLabelRef).offsetWidth,
      ),
    [],
  );

  return (
    <form className={classes.form}>
      <TextField
        id="groupName"
        label="Group Name"
        className={classes.textField}
        value={groupName}
        onChange={handleChangeText}
        margin="normal"
        variant="outlined"
        style={{ borderColor: error.groupName ? 'red' : '' }}
        fullWidth
        required
      />
      <FormControl variant="outlined" className={classes.formControl}>
        <InputLabel
          ref={ref => {
            InputLabelRef = ref;
          }}
          htmlFor="outlined-age-native-simple"
        >
          Users
        </InputLabel>
        <Select
          multiple
          value={selectedUsers}
          onChange={handleChangeSelectUsers}
          input={
            <OutlinedInput
              name="Users"
              labelWidth={labelWidth}
              id="outlined-age-simple"
            />
          }
          renderValue={selected => (
            <div className={classes.chips}>
              {selected.map(value => (
                <Chip key={value} label={value} className={classes.chip} />
              ))}
            </div>
          )}
          MenuProps={MenuProps}
        >
          {users.map(user => (
            <MenuItem key={user.keys.key} value={user.name}>
              <ListItemText primary={user.name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <FormControl variant="outlined" className={classes.formControl}>
        <InputLabel
          ref={ref => {
            InputLabelRef = ref;
          }}
          htmlFor="outlined-age-native-simple"
        >
          Groups
        </InputLabel>
        <Select
          multiple
          value={selectedGroups}
          onChange={handleChangeSelectGroups}
          input={
            <OutlinedInput
              name="Groups"
              labelWidth={labelWidth}
              id="outlined-age-simple"
            />
          }
          renderValue={selected => (
            <div className={classes.chips}>
              {selected.map(value => (
                <Chip key={value} label={value} className={classes.chip} />
              ))}
            </div>
          )}
          MenuProps={MenuProps}
        >
          {groups.map(group => (
            <MenuItem key={group.keys.key} value={group.groupName}>
              <ListItemText primary={group.groupName} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <div className={classes.btnWrapper}>
        <Button
          type="submit"
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={handleSubmit}
        >
          {loading ? (
            <CircularProgress size={22} className={classes.progress} />
          ) : (
            type
          )}
        </Button>
        <Button
          type="submit"
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={onCancel}
        >
          Cancel
        </Button>
      </div>
    </form>
  );
};

GroupProfile.propTypes = {
  classes: PropTypes.object.isRequired,
  formData: PropTypes.object.isRequired,
  error: PropTypes.object.isRequired,
  handleChangeText: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  handleChangeSelectUsers: PropTypes.func.isRequired,
  handleChangeSelectGroups: PropTypes.func.isRequired,
  type: PropTypes.string.isRequired,
  users: PropTypes.array.isRequired,
  selectedUsers: PropTypes.array.isRequired,
  selectedGroups: PropTypes.array.isRequired,
  groups: PropTypes.array.isRequired,
  onCancel: PropTypes.func.isRequired,
  loading: PropTypes.bool.isRequired,
};

export default withStyles(styles)(GroupProfile);
